<template>
  <div class="warp">
    <HEADER></HEADER>
    <div class="con">
      <div class="head">
        <div class="photo">
          <img :src="detail.image" alt="">
        </div>
        <div class="info">
          <div class="info-top">
            <div class="title">
              <div class="name">{{detail.name}}</div>
              <div class="small">
                <small>{{detail.clinicName}}</small>
                <small>{{detail.title}}</small>
              </div>
            </div>
            <div class="evaluate">
              <div class="scope">评分: 9.4</div>
              <div class="joins">1000人参与</div>
            </div>
          </div>
          <div class="info-bottom">6年专业从医经验, 耐心, 解答详细, 在全国及省级刊物上发表过多篇学术论文, 多次参加中华医学会</div>
        </div>
      </div>
      <div class="content">1990年毕业于XXXXX, 本科学历。 现任XX市中医院理事会会员, 并被特聘为中国临床医学研究中心首席专家。从事临床皮肤病的治疗二十年, 曾在天津南开医院, 北京广安门医院(中国中医研究院), 山西省医学院第二附属医院进修学习, 在全国及省级刊物上发表过多篇学术论文, 多次参加中华中医学会皮肤病分会, 积累了丰富的临床经验, 擅长治疗各种细菌, 病毒, 真菌感染性皮肤病, 变态反应性皮肤病, 尤其对银屑病(牛皮癣), 白癜风, 痤疮(粉刺), 过敏性紫癜, 各期湿疹,久治不愈的顽固性皮肤病, 用中西医结合治疗, 尤以中医为主, 疗效显著。</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import HEADER from '../../components/header';

  export default {
    name: 'detail',
    components: { HEADER },
    computed: mapState({
      details: state => state.linkDoctorView
    }),
    data() {
      return {
        detail: this.$route.params.detail
      }
    }
  }
</script>

<style lang="less" scoped>
.warp {
  height: 100%;
  display: flex;
  .con {
    flex: 1;
    padding: 3%;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    .head {
      display: flex;
      flex: 1;
      padding-bottom: 1rem;
      border-bottom: 1px solid rgb(240, 240, 240);
      .photo {
        flex: 1.2;
        display: flex;
        border: 1px solid rgb(240, 240, 240);
        justify-content: center;
        align-items: center;
      }
      .info {
        flex: 2;
        display: flex;
        flex-direction: column;
        .info-top {
          flex: 1;
          display: flex;
          .title {
            flex: 2;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: flex-start;
            padding: 0.5rem 0;
            padding-left: 5%;
            .name {
              font-size: 1.1rem;
            }
            .small {
              font-size: 0.95rem;
              color: rgb(61, 132, 194);
            }
          }
          .evaluate {
            flex: 1.2;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0.5rem 0;
            font-size: 0.75rem;
            .scope {
              color: rgb(244, 200, 138)
            }
            .joins {
              color: rgb(200, 200, 200)
            }
          }
        }
        .info-bottom {
          flex: 1.6;
          display: flex;
          justify-content: flex-start;
          color: rgb(200, 200, 200);
          font-size: 0.9rem;
          padding: 0 5%;
          text-align: left;
        }
      }
    }
    .content {
      display: flex;
      flex: 2;
      padding: 1rem 0.5rem 0 0.5rem;
      color: rgb(150, 150, 150);
      text-align: justify;
      font-size: 0.9rem;
      line-height: 1.4rem;
    }
  }
}
</style>



